<style scoped>
.fade-in {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.fade-in.active {
    opacity: 1;
}
</style>

<template>
    <div class="p-4 mt-5">
        <div
            class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom"
        >
            <h1 class="h2">{{ RouteUrlList.SysManagement.label }}</h1>
            <!-- -->
            <div class="btn-toolbar justify-content-end mb-3" role="toolbar" aria-label="Toolbar with button groups">
                <div class="btn-group me-2" role="group" aria-label="First group">
                    <!-- 新增搜尋輸入框 -->
                    <div class="input-group ms-3" style="max-width: 200px">
                        <input
                            type="text"
                            class="form-control form-control-esun"
                            placeholder="搜尋系統..."
                            v-model="appData.searchQuery"
                        />
                        <div
                            class="btn-close position-absolute end-0 top-50 translate-middle cursor-pointer shadow-none"
                            @click="clearSearch"
                            aria-label="清除搜尋"
                        >
                            <button class="btn btn-link p-0" aria-label="Close"></button>
                        </div>
                    </div>
                    <button class="btn btn-sm btn-esun-primary ml-3" @click="throttledHandleSearch">
                        <i class="bi bi-search me-1"></i>搜尋
                    </button>
                    <button class="btn btn-sm btn-esun-outline ml-3" @click="openCreateModal">
                        <i class="bi bi-plus-circle me-1"></i>新增系統
                    </button>
                    <button class="btn btn-sm btn-esun-primary ml-3" @click="openGenTokenModal">
                        <i class="bi bi-key me-1"></i>產生系統界接token
                    </button>
                </div>
                <JsonFormModal
                    v-if="createModal.showModal"
                    :myModal="createModal"
                    v-model="data"
                    @submit="handleCreateSubmit"
                    @cancel="createModal.showModal = false"
                />

                <JsonFormModal
                    v-if="genTokenModal.showModal"
                    :myModal="genTokenModal"
                    v-model="genTokenData"
                    @submit="handleGenTokenSubmit"
                    @cancel="genTokenModal.showModal = false"
                />
                <!-- 切換顯示模式 -->

                <div class="btn-group" role="group">
                    <button
                        class="btn btn-sm btn-esun-outline"
                        :class="{ active: appData.displayMode === 'card' }"
                        @click="appData.displayMode = 'card'"
                    >
                        <i class="bi bi-grid-3x3-gap me-1"></i>卡片模式
                    </button>
                    <button
                        class="btn btn-sm btn-esun-outline"
                        :class="{ active: appData.displayMode === 'table' }"
                        @click="appData.displayMode = 'table'"
                    >
                        <i class="bi bi-table me-1"></i>表格模式
                    </button>
                </div>
            </div>
        </div>

        <!-- 顯示資料 -->
        <Pagination
            v-if="appData.totalItems"
            v-model:modelValue="appData.pageNumber"
            v-model:pageSize="appData.pageSize"
            :total-items="appData.totalItems"
        />
        <div
            v-if="appData.displayMode === 'card'"
            class="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-3 active"
            :class="{ 'fade-in': appData.pageNumber }"
            ref="dataContainer"
        >
            <div class="col" v-for="(item, index) in appData.systems" :key="`${item.system}-${index}`">
                <div class="card card-esun h-100 esun-animate-in">
                    <div class="card-body">
                        <h5 class="card-title">{{ item.system }}</h5>
                        <p class="card-text">
                            <strong>ApiKey:</strong> {{ item.apiKey }}<br />
                            <strong>HashKey:</strong> {{ item.hashKey }}<br />
                            <strong>IVKey:</strong> {{ item.iVKey }}<br />
                            <strong>啟用狀態:</strong>
                            <span :class="item.isActive === 'Y' ? 'text-success' : 'text-danger'">
                                {{ item.isActive === 'Y' ? '啟用中' : '已停用' }} </span
                            ><br />
                            <strong>建立時間:</strong> {{ Common.FormatDate(item.createdTime) }}<br />
                            <strong>更新時間:</strong> {{ Common.FormatDate(item.lastUpdate) }}
                        </p>
                        <!-- 按鈕群組 -->
                        <!-- Dropdown 按鈕 -->
                        <div class="dropdown mt-3">
                            <button
                                class="btn btn-esun-outline dropdown-toggle w-100"
                                type="button"
                                data-bs-toggle="dropdown"
                                aria-expanded="false"
                            >
                                <i class="bi bi-gear me-1"></i> 操作選單
                            </button>
                            <ul class="dropdown-menu w-100">
                                <li>
                                    <a class="dropdown-item" href="#" @click.prevent="handleEdit(item)">
                                        <i class="bi bi-pencil-square me-1 text-primary"></i> 修改
                                    </a>
                                </li>
                                <li v-if="item.isActive === 'Y'">
                                    <a
                                        class="dropdown-item"
                                        href="#"
                                        @click.prevent="handleDeactivate(item)"
                                        :class="{ disabled: !item.isActive }"
                                    >
                                        <i class="bi bi-slash-circle me-1 text-warning"></i> 停用
                                    </a>
                                </li>
                                <li>
                                    <a class="dropdown-item" href="#" @click.prevent="handleGenerateToken(item)">
                                        <i class="bi bi-shield-lock me-1 text-success"></i> 產生 Token
                                    </a>
                                </li>
                            </ul>
                        </div>
                        <!-- Dropdown 按鈕 -->
                    </div>
                </div>
            </div>
        </div>

        <div
            v-else
            :class="{ 'fade-in': appData.pageNumber }"
            ref="dataContainer"
            style="overflow: auto"
            class="active"
        >
            <table class="table table-bordered table-hover align-middle">
                <thead class="table-light">
                    <tr>
                        <th>系統代號</th>
                        <th>ApiKey</th>
                        <th>HashKey</th>
                        <th>IVKey</th>
                        <th>是否啟用</th>
                        <th>建立時間</th>
                        <th>更新時間</th>
                        <th>操作選單</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in appData.systems" :key="`${item.system}-${index}`">
                        <td>{{ item.system }}</td>
                        <td>{{ item.apiKey }}</td>
                        <td>{{ item.hashKey }}</td>
                        <td>{{ item.iVKey }}</td>
                        <td>
                            <span :class="item.isActive === 'Y' ? 'text-success' : 'text-danger'">
                                {{ item.isActive === 'Y' ? '啟用中' : '已停用' }}
                            </span>
                        </td>
                        <td>{{ Common.FormatDate(item.createdTime) }}</td>
                        <td>{{ Common.FormatDate(item.lastUpdate) }}</td>
                        <td>
                            <!-- Dropdown 按鈕 -->
                            <div class="dropdown mt-3">
                                <button
                                    class="btn btn-esun-outline dropdown-toggle w-100"
                                    type="button"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false"
                                >
                                    <i class="bi bi-gear me-1"></i> 操作選單
                                </button>
                                <ul class="dropdown-menu w-100">
                                    <li>
                                        <a class="dropdown-item" href="#" @click.prevent="handleEdit(item)">
                                            <i class="bi bi-pencil-square me-1 text-primary"></i> 修改
                                        </a>
                                    </li>
                                    <li v-if="item.isActive === 'Y'">
                                        <a
                                            class="dropdown-item"
                                            href="#"
                                            @click.prevent="handleDeactivate(item)"
                                            :class="{ disabled: !item.isActive }"
                                        >
                                            <i class="bi bi-slash-circle me-1 text-warning"></i> 停用
                                        </a>
                                    </li>
                                    <li>
                                        <a class="dropdown-item" href="#" @click.prevent="handleGenerateToken(item)">
                                            <i class="bi bi-shield-lock me-1 text-success"></i> 產生 Token
                                        </a>
                                    </li>
                                </ul>
                            </div>
                            <!-- Dropdown 按鈕 -->
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <JsonFormModal
            v-if="updateModal.showModal"
            :myModal="updateModal"
            v-model="updateData"
            @submit="handleUpdateSubmit"
            @cancel="updateModal.showModal = false"
        />

        <Pagination
            v-if="appData.totalItems"
            v-model:modelValue="appData.pageNumber"
            v-model:pageSize="appData.pageSize"
            :total-items="appData.totalItems"
        />
    </div>
</template>

<script setup lang="ts">
import { ref, onMounted, watch, computed, reactive } from 'vue';
defineProps<{ collapsed: boolean; paginationType?: 'client' | 'server' }>();
defineEmits(['toggleTheme']);
import Pagination from '@/components/pagination/Pagination.vue';
import * as Common from '@/libs/Common';
import type { SystemListManagementViewDataResp } from '@/api/v1/2_Sys/SystemListManagementViewData';
import type { SystemDTO } from '@/models/DTO/SystemDTO';
import * as ApiClient from '@/libs/ApiClient';
import API from '@/api/v1/';
import JsonFormModal from '@/components/modal/JsonFormModal/JsonFormModal.vue';
import { GetCreateSystemModal, GetDefaultFormData, type FileFormData } from './CreateSystemModal';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import { RouteUrlList } from '@/libs/RouteList';

// system 資料（ API 傳入）
interface IAppData {
    systems: SystemDTO[];
    searchQuery: string;
    pageNumber: number;
    pageSize: number;
    totalItems: number;
    displayMode: 'card' | 'table';
    paginationType: 'server' | 'client';
}

const appData = reactive<IAppData>({
    systems: [],
    searchQuery: '',
    pageNumber: 1,
    pageSize: 10,
    totalItems: 0,
    displayMode: 'card',
    paginationType: 'client'
});

// 建立system 相關
const createModal = ref<MyModal>(GetCreateSystemModal());
const data = ref<FileFormData>(GetDefaultFormData());

/**
 * @todo 載入資料
 */
const LoadViewData = async () => {
    try {
        const response = await API.SystemListManagementViewData(
            appData.searchQuery,
            appData.pageNumber,
            appData.pageSize
        );
        if (response.status && response.result?.items) {
            appData.systems = response.result.items;

            appData.totalItems = response.result.totalCount;
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    } catch (error) {
        console.error('Error fetching systems:', error);
        // Handle error (e.g., show an error message)
        ApiClient.HandleError(error);
    } finally {
    }
};

onMounted(async () => {
    await LoadViewData();
});

watch(
    () => ({
        pageNumber: appData.pageNumber,
        pageSize: appData.pageSize
    }),
    () => {
        LoadViewData();
    }
);

// 搜尋系統資料
const handleSearch = async () => {
    if (!appData.searchQuery) {
        return;
    }
    await LoadViewData();
};
// 使用節流函式包裝ionput事件處理器，提高效能
const throttledHandleSearch = Common.Throttle(handleSearch, 100);

watch(
    () => ({
        searchQuery: appData.searchQuery
    }),
    () => {
        appData.pageNumber = 1;
    }
);

const clearSearch = () => {
    appData.searchQuery = '';
    LoadViewData();
};

/**
 * @todo 展開新增系統 Modal
 */
function openCreateModal() {
    createModal.value.showModal = true;
}
async function handleCreateSubmit(formData: Record<string, any>) {
    try {
        createModal.value.showModal = false;

        const item = formData as SystemDTO;
        let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
            SimplePopUpModalType.Warning,
            '警告',
            `確定要新增系統 ${formData.system} 嗎？`,
            [
                {
                    id: 'cancel',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-esun-outline ',
                    resolveParam: 'cancel',
                    title: '取消'
                },
                {
                    id: 'confirm',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-info',
                    resolveParam: 'confirm',
                    title: '確定'
                }
            ]
        );

        if (action.param === 'confirm') {
            // 更新邏輯
            alert('更新資料：\n' + JSON.stringify(formData, null, 2));

            const response = await API.SystemListManagementCreate(item);
            if (response.status) {
                simplePopUp
                    .showAlert(
                        SimplePopUpModalType.Success,
                        '成功',
                        `系統 ${item.system} 已新增。`,
                        [
                            {
                                id: 'ok',
                                type: SimplePopUpBtnType.callback,
                                btnClass: 'btn-info',
                                resolveParam: 'ok',
                                title: '確定'
                            }
                        ],
                        SimplePopUpSize.lg
                    )
                    .then(async () => {
                        await LoadViewData();
                    });
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        }
        createModal.value.showModal = false;
    } catch (error) {
        ApiClient.HandleError(error);
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    }
}

// 產生token
import { GetDefaultTokenFormData, GetGenTokenModal, getNowString, type GenTokenFormData } from './GenTokenModal';
const genTokenModal = ref<MyModal>(GetGenTokenModal());
const genTokenData = ref<GenTokenFormData>(GetDefaultTokenFormData());

import { encryptObject, decryptObject } from '@/libs/AES_Helper';
import { type Source } from '@/models/DTO/Source';
import { GetUpdateSystemModal } from './UpdateSystemModal';
/**
 * @todo 展開新增系統 Modal
 */
function openGenTokenModal() {
    genTokenModal.value.showModal = true;
    genTokenData.value = GetDefaultTokenFormData();
}

/**
 * @todo 產生 Token
 * @param _formData 表單資料
 */
function handleGenTokenSubmit(_formData: Record<string, any>) {
    try {
        if (!_formData) {
            throw new Error('Form data is required');
        }
        const formData = _formData as GenTokenFormData;
        if (!formData.time) {
            formData.time = getNowString();
        }
        // alert('提交資料：\n' + JSON.stringify(formData, null, 2));
        // UTF-8 key/IV（測試用，生產建議用 hex）
        const key = formData.hash; // 32 bytes for AES-256
        const iv = formData.iv; // 16 bytes

        // 加密
        const source: Source = { source_id: formData.source_id, time: formData.time, AES_key: formData.AES_key };
        const encrypted = encryptObject(source, key, iv, 'utf8');
        console.log('Encrypted:', encrypted);
        genTokenData.value.encrypted_source = encrypted;

        // 解密
        const decrypted = decryptObject<Source>(encrypted, key, iv, 'utf8');
        console.log('Decrypted:', decrypted);
        genTokenData.value.decrypted_source = JSON.stringify(decrypted, null, 2);

        genTokenModal.value.showModal = true;
    } catch (error) {
        ApiClient.HandleError(error);
    }
}
// 卡片 事件處理函式
// 建立system 相關
import {
    SimplePopUp,
    SimplePopUpBtnType,
    SimplePopUpModalType,
    SimplePopUpSize,
    type SimplePopupResolveResult
} from '@/libs/SimplePopUp';
const simplePopUp = new SimplePopUp();

const updateModal = ref<MyModal>(GetUpdateSystemModal());
const updateData = ref<SystemDTO>({
    system: '',
    apiKey: '',
    hashKey: '',
    iVKey: '',
    isActive: '',
    createdTime: '',
    lastUpdate: ''
});

function handleEdit(item: SystemDTO) {
    // 編輯邏輯
    updateData.value = { ...item }; // 深拷貝，避免直接修改原始資料
    updateModal.value.showModal = true;
}

async function handleUpdateSubmit(formData: Record<string, any>) {
    try {
        const item = formData as SystemDTO;
        let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
            SimplePopUpModalType.Warning,
            '警告',
            `確定要更新系統 ${formData.system} 嗎？`,
            [
                {
                    id: 'cancel',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-esun-outline ',
                    resolveParam: 'cancel',
                    title: '取消'
                },
                {
                    id: 'confirm',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-info',
                    resolveParam: 'confirm',
                    title: '確定'
                }
            ]
        );

        if (action.param === 'confirm') {
            // 更新邏輯
            alert('更新資料：\n' + JSON.stringify(formData, null, 2));

            const response = await API.SystemListManagementUpdate(item);
            if (response.status) {
                simplePopUp
                    .showAlert(
                        SimplePopUpModalType.Success,
                        '成功',
                        `系統 ${item.system} 已更新。`,
                        [
                            {
                                id: 'ok',
                                type: SimplePopUpBtnType.callback,
                                btnClass: 'btn-info',
                                resolveParam: 'ok',
                                title: '確定'
                            }
                        ],
                        SimplePopUpSize.lg
                    )
                    .then(async () => {
                        await LoadViewData();
                    });
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        }
        updateModal.value.showModal = false;
    } catch (error) {
        ApiClient.HandleError(error);
    }
}

/**
 * @todo 停用 系統
 * @param item
 */
async function handleDeactivate(item: SystemDTO) {
    // 停用邏輯
    let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
        SimplePopUpModalType.Warning,
        '警告',
        `確定要停用系統 ${item.system} 嗎？`,
        [
            {
                id: 'cancel',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-esun-outline ',
                resolveParam: 'cancel',
                title: '取消'
            },
            {
                id: 'confirm',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-info',
                resolveParam: 'confirm',
                title: '確定'
            }
        ]
    );

    if (action.param === 'confirm') {
        const response = await API.SystemDeactivate(item.system);
        if (response.status) {
            // 停用成功，Reload 資料
            simplePopUp
                .showAlert(
                    SimplePopUpModalType.Success,
                    '成功',
                    `系統 ${item.system} 已停用。`,
                    [
                        {
                            id: 'ok',
                            type: SimplePopUpBtnType.callback,
                            btnClass: 'btn-info',
                            resolveParam: 'ok',
                            title: '確定'
                        }
                    ],
                    SimplePopUpSize.lg
                )
                .then(async () => {
                    await LoadViewData();
                });
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    }
}

/**
 * @todo 產生 token
 * @param item
 */
function handleGenerateToken(item: SystemDTO) {
    // 產生 token 邏輯
    genTokenModal.value.showModal = true;
    genTokenData.value = {
        // 深拷貝，避免直接修改原始資料
        source_id: item.system,
        time: getNowString(),
        AES_key: item.apiKey,
        hash: item.hashKey,
        iv: item.iVKey,
        encrypted_source: '',
        decrypted_source: ''
    };
}
</script>
