import { ref, onMounted, watch, computed } from 'vue';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
// 定義測試欄位群組與欄位
// AES-256{"source_id":"System", time: "{yyyy/MM/dd HH:mm:ss}", "AES_key": "ApiKey" }
export const GetGenTokenModal = (): MyModal => {
    return {
        modalId: 'GenTokenModal',
        title: '產生 X-Source',
        showModal: false,
        size: 'modal-xl',
        staticBackdrop: true,
        scrollable: true,
        fieldGroups: [
            {
                title: '',
                gridClass: 'col-6  ',
                fields: [
                    {
                        modelId: 'source_id',
                        name: 'source_id',
                        type: 'text',
                        label: 'source_id 系統代號(識別)',
                        placeholder: 'system',
                        required: true,
                        maxlength: 10,
                        onInputCallback: (value: string) => {
                            console.log(123);
                        }
                    },
                    {
                        modelId: 'time',
                        name: 'time',
                        type: 'text',
                        label: 'time',
                        hint: '(yyyy/MM/dd HH:mm:ss)',
                        maxlength: 19,
                        required: false,
                        onChangeCallback: (value: string) => {
                            if (!value) {
                                value = getNowString();
                            }
                        }
                    },
                    {
                        modelId: 'AES_key',
                        name: 'AES_key',
                        type: 'text',
                        label: 'AES_key(即ApiKey)',
                        hint: '(ApiKey格式為UUID)',
                        maxlength: 36,
                        required: true
                    }
                ]
            },
            {
                title: '',
                gridClass: 'col-6  ',
                fields: [
                    {
                        modelId: 'hash',
                        name: 'hash',
                        type: 'text',
                        label: 'HashKey',
                        placeholder: 'HashKey',
                        required: true,
                        maxlength: 32,
                        onInputCallback: (value: string) => {
                            console.log(123);
                        }
                    },
                    {
                        modelId: 'iv',
                        name: 'iv',
                        type: 'text',
                        label: 'IVKey',
                        placeholder: 'IVKey',
                        required: true,
                        maxlength: 16,
                        onInputCallback: (value: string) => {
                            console.log(123);
                        }
                    }
                ]
            },
            {
                title: '',
                gridClass: 'col-12  ',
                fields: [
                    {
                        modelId: 'encrypted_source',
                        name: 'Encrypted Source',
                        type: 'textarea',
                        label: 'Encrypted',
                        placeholder: 'Encrypted',
                        required: false,
                        disabled: true,
                        maxlength: 1000,
                        rows: 5,
                        onInputCallback: (value: string) => {
                            console.log(123);
                        }
                    },
                    {
                        modelId: 'decrypted_source',
                        name: 'Decrypted Source',
                        type: 'textarea',
                        label: 'Decrypted',
                        placeholder: 'Decrypted',
                        required: false,
                        disabled: true,
                        maxlength: 1000,
                        rows: 5,
                        onInputCallback: (value: string) => {
                            console.log(123);
                        }
                    }
                ]
            }
        ]
    };
};

export interface GenTokenFormData {
    source_id: string;
    time: string | null;
    AES_key: string;
    hash: string;
    iv: string;
    encrypted_source: string;
    decrypted_source: string;
}
export const GetDefaultTokenFormData = (): GenTokenFormData => {
    return {
        source_id: '',
        time: getNowString(),
        AES_key: '',
        hash: '',
        iv: '',
        encrypted_source: '',
        decrypted_source: ''
    };
};

export function getNowString(): string {
    const now = new Date();

    const pad = (n: number) => n.toString().padStart(2, '0');

    const yyyy = now.getFullYear();
    const MM = pad(now.getMonth() + 1);
    const dd = pad(now.getDate());

    const HH = pad(now.getHours());
    const mm = pad(now.getMinutes());
    const ss = pad(now.getSeconds());

    return `${yyyy}/${MM}/${dd} ${HH}:${mm}:${ss}`;
}
