import { ref, onMounted, watch, computed } from 'vue';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import * as Common from '@/libs/Common';

// 定義測試欄位群組與欄位
export const GetUpdateSystemModal = (): MyModal => {
    return {
        modalId: 'sysUpdateModal',
        title: '修改介接系統',
        showModal: false,
        size: 'modal-xl',
        staticBackdrop: true,
        fieldGroups: [
            {
                title: '',
                gridClass: 'col-12  ',
                fields: [
                    {
                        modelId: 'system',
                        name: 'system',
                        type: 'text',
                        label: '系統代號(識別)',
                        placeholder: '系統代號',
                        required: true,
                        maxlength: 10,
                        onInputCallback: (value: string) => {
                            return (value = Common.ReplaceSpecialChar(value));
                        }
                    },
                    {
                        modelId: 'apiKey',
                        name: 'apiKey',
                        type: 'text',
                        label: 'ApiKey',
                        hint: '(格式為UUID 選填，若空則系統自動產生)',
                        maxlength: 36,
                        required: false,
                        onInputCallback: (value: string) => {
                            value = Common.ReplaceSpecialChar(value);
                        }
                    },

                    {
                        modelId: 'hashKey',
                        name: 'hashKey',
                        type: 'text',
                        label: 'hashKey',
                        hint: '',
                        maxlength: 36,
                        required: false,
                        onInputCallback: (value: string) => {
                            value = Common.HtmlspecialChars(value);
                        }
                    },

                    {
                        modelId: 'iVKey',
                        name: 'iVKey',
                        type: 'text',
                        label: 'iVKey',
                        hint: ' ',
                        maxlength: 16,
                        required: false,
                        onInputCallback: (value: string) => {
                            value = Common.HtmlspecialChars(value);
                        }
                    },

                    {
                        modelId: 'isActive',
                        name: 'isActive',
                        type: 'radio',
                        label: '是否啟用',
                        inline: true,
                        options: [
                            { label: '啟用', value: 'Y' },
                            { label: '停用', value: 'N' }
                        ],
                        required: true
                    }
                ]
            }
        ]
    };
};

export interface FileFormData {
    system: string;
    apiKey: string | null;
    isActive: 'Y' | 'N';
}
export const GetDefaultFormData = (): FileFormData => {
    return {
        system: '',
        apiKey: null,
        isActive: 'Y'
    };
};
