// AES_Helper.ts
import CryptoJS from 'crypto-js';

type Encoding = 'utf8' | 'hex' | 'base64';

/**
 * 將指定格式的字串轉為 WordArray（CryptoJS 使用格式）
 */
function parseToWordArray(value: string, encoding: Encoding): CryptoJS.lib.WordArray {
    switch (encoding) {
        case 'utf8':
            return CryptoJS.enc.Utf8.parse(value);
        case 'hex':
            return CryptoJS.enc.Hex.parse(value);
        case 'base64':
            return CryptoJS.enc.Base64.parse(value);
        default:
            throw new Error('Unsupported encoding type.');
    }
}

/**
 * AES-CBC 加密任意物件，傳回 Base64 字串
 */
export function encryptObject<T extends object>(
    obj: T,
    keyStr: string,
    ivStr: string,
    encoding: Encoding = 'utf8'
): string {
    const key = parseToWordArray(keyStr, encoding);
    const iv = parseToWordArray(ivStr, encoding);

    if (![16, 24, 32].includes(key.sigBytes)) {
        throw new Error(`Invalid AES key size: ${key.sigBytes} bytes`);
    }
    if (iv.sigBytes !== 16) {
        throw new Error(`IV must be 16 bytes, got ${iv.sigBytes} bytes`);
    }

    const plaintext = JSON.stringify(obj);

    const encrypted = CryptoJS.AES.encrypt(plaintext, key, {
        iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });

    return encrypted.toString(); // Base64 string
}

/**
 * AES-CBC 解密，回傳原始物件
 */
export function decryptObject<T>(
    encryptedBase64: string,
    keyStr: string,
    ivStr: string,
    encoding: Encoding = 'utf8'
): T {
    const key = parseToWordArray(keyStr, encoding);
    const iv = parseToWordArray(ivStr, encoding);

    if (![16, 24, 32].includes(key.sigBytes)) {
        throw new Error(`Invalid AES key size: ${key.sigBytes} bytes`);
    }
    if (iv.sigBytes !== 16) {
        throw new Error(`IV must be 16 bytes, got ${iv.sigBytes} bytes`);
    }

    const decrypted = CryptoJS.AES.decrypt(encryptedBase64, key, {
        iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });

    const jsonStr = decrypted.toString(CryptoJS.enc.Utf8);
    return JSON.parse(jsonStr) as T;
}
