import { ref, onMounted, watch, computed } from 'vue';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import * as Common from '@/libs/Common';

// 定義測試欄位群組與欄位
export const GetCreateSystemModal = (): MyModal => {
    return {
        modalId: 'myModal',
        title: '新增介接系統',
        showModal: false,
        size: 'modal-xl',
        staticBackdrop: true,
        fieldGroups: [
            {
                title: '',
                gridClass: 'col-12  ',
                fields: [
                    {
                        modelId: 'system',
                        name: 'system',
                        type: 'text',
                        label: '系統代號(識別)',
                        placeholder: '系統代號',
                        required: true,
                        maxlength: 10,
                        onInputCallback: (value: string) => {
                            return Common.ReplaceSpecialChar(value);
                        },
                        onSubmitCheckCallback: (value: string) => {
                            if (!value) {
                                return '系統代號(識別)為必填欄位';
                            }
                            if (value.length > 10) {
                                return '系統代號(識別)長度不能超過10個字元';
                            }
                            return '';
                        }
                    },
                    {
                        modelId: 'apiKey',
                        name: 'apiKey',
                        type: 'text',
                        label: 'ApiKey',
                        hint: '(格式為UUID 選填，若空則系統自動產生)',
                        maxlength: 36,
                        required: false,
                        onInputCallback: (value: string) => {
                            return Common.ReplaceSpecialChar(value);
                        },
                        onSubmitCheckCallback: (value: string) => {
                            if (value && value.length > 36) {
                                return 'ApiKey長度不能超過36個字元';
                            } else if (value && !Common.IsUUID(value)) {
                                return 'ApiKey格式錯誤，應為UUID格式';
                            }
                            return '';
                        }
                    },
                    {
                        modelId: 'isActive',
                        name: 'isActive',
                        type: 'radio',
                        label: '是否啟用',
                        inline: true,
                        options: [
                            { label: '啟用', value: 'Y' },
                            { label: '停用', value: 'N' }
                        ],
                        required: true
                    }
                ]
            }
        ]
    };
};

export interface FileFormData {
    system: string;
    apiKey: string | null;
    isActive: 'Y' | 'N';
}
export const GetDefaultFormData = (): FileFormData => {
    return {
        system: '',
        apiKey: null,
        isActive: 'Y'
    };
};
